*{
 box-sizing: border-box;
 margin: 0;
 padding: 0;
}

body{
  font-family: Arial, Helvetica, sans-serif;
  line-height: 1.4;
}

a{
  color: #333;
  text-decoration: none;
}

.container{
  padding: 0 1rem;
}

.btn{
  display: inline-block;
  cursor: pointer;
  border: none;
  background: #555;
  color: #fff;
  padding: 6px 20px;
}

.btn:hover{
  background: #666;
}